import { lazy } from 'react'
import { Link, Route, useHistory } from 'react-router-dom'
import { $LOGIN, $REGISTER } from 'routes'
import OtherLoginOption from './OtherLoginOption'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowAltCircleLeft } from '@fortawesome/free-solid-svg-icons'
import { ViewLoginContainer } from './style'

const FormLogin = lazy(() => import('./FormLogin'))
const FormRegister = lazy(() => import('./FormRegister'))

const Login = () => {
  const history = useHistory()

  const handleNavigationBack = () => history.goBack()

  return <ViewLoginContainer>
    <div className="boxForm">
      <button onClick={handleNavigationBack}><FontAwesomeIcon icon={faArrowAltCircleLeft} /></button>
      <Route path={$LOGIN}>
        <h2>Iniciar Sesión</h2>
        <FormLogin />
        <div className="textRedirect">
          <span>¿Ya estás registrado?</span><Link to={$REGISTER}>Regístrate</Link>
        </div>
      </Route>
      <Route path={$REGISTER}>
        <h2>Registrarse</h2>
        <FormRegister />
      </Route>
      <div className="lineDivisor"><hr /><span><strong>Or</strong></span></div>
      <OtherLoginOption />
      <a href="#" rel="noopener">politicas de privacidad</a>
    </div>
  </ViewLoginContainer>
}

export default Login